import React, { useEffect, useState } from 'react';

import Home  from './components/home.js';
import Projects  from './components/projects.js';
import Qualifications  from './components/qualifications.js';
import Contact  from './components/contact.js';

import emailjs from 'emailjs-com';
import Swal from 'sweetalert2';

const SERVICE_ID = "service_7sw9z7b";
const TEMPLATE_ID = "template_xyxlc9j";
const USER_ID = "user_mStLOtoHlSMPecsUt6SbK";

function App() {
  const [selected, setSelected] = useState("home");
  
  const handleClick = (e) => {
    if(e.target.value === undefined) {
      setSelected("home");
    } else {
      setSelected(e.target.value);
    }
  }

  const handleSubmit = (e) => {
    e.preventDefault();
    emailjs.sendForm(SERVICE_ID, TEMPLATE_ID, e.target, USER_ID)
      .then((result) => {
        console.log(result.text);
        Swal.fire({
          icon: 'success',
          title: 'Message sent successfully'
        })
      }, (error) => {
        console.log(error.text);
        Swal.fire({
          icon: 'error',
          title: 'Ooops, something went wrong',
          text: error.text,
        })
      });
    e.target.reset()
  };

  useEffect(() => {
    
  }, [selected]);

  if (selected === "home") {
    return (
      <Home handleClick={handleClick} />
    );
  } else if (selected === "projects") {
    return (
      <Projects handleClick={handleClick} />
    );
  } else if (selected === "qualifications") {
    return (
      <Qualifications handleClick={handleClick} />
    );
  } else if (selected === "contact") {
    return (
      <Contact handleClick={handleClick} handleSubmit={handleSubmit} />
    );
  } else {
    return (
      <div>
        <p>Page not finished</p>
        <button onClick={handleClick} value="home">Go back?</button>
      </div>
    )
  }
  
}

export default App;
